* {
	box-sizing: border-box;
}
html {
	height: 100%;
}
body {
	height: 100%;
	margin: 0;
	padding: 0;
	background: #fff;
	font-family: 'Open Sans', sans-serif;
}

a {
	cursor: pointer;
}
.radial-bg {
	background: #2d77bb;
	background: radial-gradient(circle, #8dd5ff 0%, #268ff8 100%);
}
.flex-center {
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: center;
	justify-content: center;
}
.white-plate {
	background-color: #fff;
	border-radius: 16px;
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
	padding-left: 40px;
	padding-right: 40px;
	padding-bottom: 55px;
	box-shadow: 0px 10px 62px 10px #133b9a;
}
.white-plate.white-plate--payment {
	max-width: 400px;
	padding-bottom: 25px;
	box-shadow: 0px 10px 62px 10px rgba(47, 128, 214, 0.71);
}
.white-plate__header {
	padding-top: 25px;
	padding-bottom: 25px;
}
.white-plate__logo {
	font-weight: 800;
	font-size: 34px;
	color: #2789e6;
	transition: 0.2s ease-in;
	margin: 0;
}
.white-plate__logo span {
	color: #2b2b2b;
}
@media (max-width: 576px) {
	.white-plate__logo {
		display: block;
		text-align: center;
	}
}
.white-plate__line-between {
	background-color: #2d93f0;
	height: 60px;
	margin-left: -55px;
	margin-right: -55px;
	margin-bottom: 40px;
}
.white-plate__line-between--main {
	background-color: #2d93f0;
	height: 45px;
	margin-bottom: 30px;
}
.body--dashboard {
	padding-left: 330px;
}
.left-panel {
	background-color: #094071;
	width: 300px;
	height: 100%;
	left: 0;
	top: 0;
	bottom: 0;
	color: #fff;
	position: fixed;
	z-index: 1;
}
.left-panel__logo {
	padding-top: 41px;
	padding-left: 50px;
	padding-bottom: 40px;
	border-bottom: 1px solid #14497a;
}
.left-panel__logo-title {
	font-family: 'Montserrat', sans-serif;
	color: #fff;
	font-size: 33px;
	font-weight: 700;
}
.left-panel__logo-subtitle {
	color: #8aacce;
	font-size: 14px;
	font-weight: 400;
	text-transform: uppercase;
}
.left-panel__user {
	padding-left: 46px;
	padding-right: 30px;
	padding-top: 20px;
	padding-bottom: 20px;
	border-bottom: 1px solid #14497a;
}
.left-panel__user-photo {
	width: 56px;
	height: 56px;
	border-radius: 80px;
	float: left;
	overflow: hidden;
}
.left-panel__user-photo img {
	max-width: 100%;
	height: auto;
}
.left-panel__user-name {
	font-size: 18px;
	line-height: 1.3;
	font-weight: 600;
	margin-left: 76px;
}
.left-panel__navigation {
	color: #cbdceb;
	font-size: 16px;
	padding-left: 50px;
	padding-top: 35px;
	padding-bottom: 15px;
	border-bottom: 1px solid #14497a;
}
.left-panel__navigation--no-line {
	border-bottom: none;
}
.left-panel__navigation-title {
	font-size: 18px;
	color: #8eb6da;
	font-weight: 200;
	margin-bottom: 10px;
	text-transform: uppercase;
}
.left-panel__navigation ul {
	margin: 0;
	padding: 0;
	list-style-type: none;
}
.left-panel__navigation ul li {
	margin: 0;
	padding: 0;
	margin-bottom: 0px;
}
.left-panel__navigation a {
	color: #cbdceb;
	display: block;
	width: 280px;
	position: relative;
	z-index: 2;
	transition: 250ms all ease-in;
	margin-left: -31px;
	padding-left: 31px;
	padding-top: 15px;
	padding-bottom: 18px;
	background-color: #094071;
	border-radius: 80px 0px 0px 80px;
	transition: 100ms all ease-out;
}
.left-panel__navigation a:hover,
.left-panel__navigation a.active {
	text-decoration: none;
	background-color: #2865b1;
	color: #fff;
}
.left-panel__navigation .badge {
	background-color: #2865b1;
	color: #fff;
	border-radius: 32px;
	padding: 6px 10px 5px;
	line-height: 1;
	font-size: 14px;
}
.left-panel__navigation .badge:first-child {
	margin-left: 5px;
}
.main-wrapper {
	margin-left: 0px;
	padding-top: 0px;
	padding-bottom: 20px;
}
.form-wrapper {
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
}
.admin-heading-1 {
	font-size: 34px;
	font-weight: 200;
	margin: 20px 0 10px;
}
.badge {
	padding: 6px 10px 5px;
	font-size: 14px;
}
