/* Floats */
.float-right {
	float: right;
}
.float-left {
	float: left;
}
.clearfix::after,
.clearfix::before {
	content: '';
	display: table;
	clear: both;
}
/* Text align */
.text-center {
	text-align: center;
}
.text-left {
	text-align: left;
}
.text-right {
	text-align: right;
}
.text-justify {
	text-align: justify;
}
/* Font weight */
.light {
	font-weight: 300;
}
.normal {
	font-weight: 400;
}
.strong {
	font-weight: 700;
}

.project-nav {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	height: 60px;
	z-index: 999;

	background-color: #e3f7ff;
	color: #fff;
}

.project-nav__links-wrapper {
	display: flex;
	justify-content: center;
	column-gap: 30px;
	align-items: center;
	height: 100%;
}

.project-nav a {
	color: rgb(86, 127, 192);
}

body.with-nav {
	padding-top: 60px;
}

body.with-nav .left-panel {
	top: 60px;
	bottom: 0px;
	height: auto;
	overflow-x: hidden;
	overflow-y: auto;
}
